rams.id }, '/lk/api/quiz-answer/show' ).then(response => {\r\n        this.quiz = response.data.props;\r\n      });\r\n    },\r\n    consoleLogJSON(item){\r\n      console.group(item.quiz.name+\" steps\");\r\n      console.log(JSON.stringify(item.steps, null, 2));\r\n      console.groupEnd();\r\n    },\r\n  }\r\n}\r\n</script>\r\n<style>\r\n.stat_wr td{\r\n  vertical-align: middle;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
